<template>
    <div class='c-box'>
        <Row class='c-row'>
           <i-col class-name='mysorch' :xs="{span:24}" :sm="{span:24}" :lg="{span:24}">
                <div class='setbox' >
                    <div style="position:fixed" v-if='flng'>
                         <Anchor style="position:fixed" show-ink>
                            <AnchorLink href="#Personalinformation" title="" />
                            <AnchorLink href="#Personalintroduction" title="" />
                            <AnchorLink href="#workexperience" title=""/>
                            <AnchorLink href="#Selfdescription" title="" />
                            <AnchorLink href="#Skill" title="" />
                        </Anchor>
                    </div>

                    <div :class="flng==true ? 'content' : 'content-m'">
                        <div id='Personalinformation'>
                            <Stitle title=''/>
                            <div class='Personalinformation-content'>
                                <div style="width:100px;height:100px;border-radius:50%;overflow:hidden;margin-top:20px">
                                    <img style="width 100%;height:100%" src="../assets/lookme.jpg" alt="">
                                </div>
                                
                                <span style="font-size:16px;font-weight:bold"></span>
                                <span>,</span>
                                <span></span>
                                <div class='info'>
                                    <p></p>
                                    <Divider type="vertical" />
                                    <p>24</p>
                                    <Divider type="vertical" />
                                    <p></p>
                                    <Divider type="vertical" />
                                    <p>2</p>
                                    <Divider type="vertical" />
                                    <p></p>
                                </div>
                                <span>13454143096</span>
                                <span>1026248690@qq.com</span>
                            </div>
                        </div>

                        <div id='Personalintroduction'>
                            <Stitle title=''/>
                            <ul class='setul'>
                                <li>htmlcss</li>
                                <li>javascriptes6es7</li>
                                <li>reactreact-routermobxreact</li>
                                <li>webpack</li>
                                <li>expressmongodbnodejs</li>
                            </ul>
                        </div>


                        <div id='workexperience'>
                            <Stitle title=''/>

                            <Timeline style='margin-top:20px'>
                                <TimelineItem>
                                    <p class="time">2016-12  2018-8</p>
                                    <p style='font-size:14px;font-weight:bold'></p>
                                    <p></p>
                                    <p></p>
                                </TimelineItem>
                                <TimelineItem>
                                    <p class="time">2018-9  </p>
                                    <p style='font-size:14px;font-weight:bold'></p>
                                    <p></p>
                                    <p></p>
                                </TimelineItem>
                                
                            </Timeline>
                        </div>

                        <div id='Selfdescription'>
                            <Stitle title=''/>
                            <ul  class='setul'>
                                <li>162</li>
                                <li></li>
                                <li></li>
                            </ul>
                        </div>

                        <div id='Skill'>
                            <Stitle title=''/>
                            <div :class="flng==true ? 'skill-item' : 'skill-item-m'" v-for='item in dataItems'>
                                <span style="color:red">{{item.title}}</span>
                                <Progress :percent="item.num" status="active" />
                            </div>
                        </div>
                    </div>
                </div>
            </i-col>
            
        </Row>
    </div>
</template>

<script>
import RigthView from '../plugins/rightView'
import Stitle from '../plugins/sign'
export default {
    data(){
        return {
          dataItems:[
              {
                  title:'jascript',
                  num:70
              },
              {
                  title:'vue',
                  num:60
              },
              {
                  title:'html',
                  num:80
              },
              {
                  title:'css',
                  num:80
              },
              {
                  title:'node',
                  num:50
              },
              {
                  title:'mongodb',
                  num:50
              },
              {
                  title:'webpack',
                  num:50
              },
              {
                  title:'fetch',
                  num:70
              },

          ],
          flng:true,
        }
    },
    created(){
       
    },
    methods:{
        resize(e){
            if(e.target.innerWidth<800){
                this.flng=false;
            }else{
                this.flng=true
            }
        }
    },
    mounted(){
        window.onresize=this.resize
           

        
    },
    components:{
        RigthView,
        Stitle
    }
}
</script>

<style lang="stylus" scoped>
    .skill-item
        width 50%
        display flex
        flex-direction column
    .skill-item-m
        width 100%
        display flex
        flex-direction column
    #Skill
        margin-top 30px
        display flex
        flex-direction column
        align-items center
    #Selfdescription
        margin-top 30px
        display flex
        flex-direction column
        align-items center
    #workexperience
        margin-top 30px
        display flex
        flex-direction column
        align-items center
    .setul
        width auto
        margin-top 20px
        li
            font-size 14px
    #Personalintroduction
        margin-top 30px
        display flex
        flex-direction column
        align-items center
    .Personalinformation-content
        width 100%
        display flex
        flex-direction column
        align-items center
        font-sie 14px
        span    
            margin-top 10px
        .info
            display flex
            align-items center
            margin-top 10px

    #Personalinformation
        display flex
        flex-direction column
        align-items center
        
    .content
        width 100%
        margin-left 100px
        background white
        border-radius 10px
        padding 10px
        overflow hidden
    .content-m
        width 100%
        background white
        border-radius 10px
        padding 10px
        overflow hidden
    .setbox
        width 100%
        margin-top 10px
        display flex
        justify-content space-between
    .class-box
        width 100%
        background white
        border-radius 10px
        margin-top 20px
        padding 20px
    .c-box
        width 100%
        height 100%
        display flex
        justify-content center
        .c-row
            width 95%
            height auto 
            display flex
            flex-direction column
            

</style>





